.option-container {
  height: fit-content;
  padding: 10px;
  font-size: 16px;
}
.options {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  font-size: 1em;
  flex-direction: row;
}

.map {
  text-align: center;
}

.item {
  margin-top: 10px;
  margin-right: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.i {
  margin-right: 10px;
}

.unvisitedNode {
  border-left: 0.1px solid rgb(188, 188, 255);
}

@media (max-width: 700px) {
  .option-container {
    font-size: 14px;
  }
  .item {
    margin-top: 8px;
    margin-right: 20px;
  }
}

@media (max-width: 500px) {
  .option-container {
    font-size: 10px;
  }
  .item {
    margin-top: 8px;
    margin-right: 20px;
  }
}
