.node {
  height: 28px;
  width: 28px;
  border: 0.1px solid rgb(188, 188, 255);
  border-left: none;
  border-top: none;
  cursor: pointer;
  overflow: hidden;
}
.node:nth-child(1) {
  border-top: 0.1px solid rgb(188, 188, 255);
}

.wall {
  animation: plotWall 0.11s ease;
  background-color: #0c3547;
  border: none;
}

@keyframes plotWall {
  0% {
    transform: scale(0.5);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.startNode {
  background-image: url(https://clementmihailescu.github.io/Pathfinding-Visualizer/public/styling/triangletwo-right.svg);
  background-repeat: no-repeat;
  background-size: 26px;
  background-color: green;
  cursor: move;
}

.finishNode {
  background-image: url(https://clementmihailescu.github.io/Pathfinding-Visualizer/public/styling/circle.svg);
  background-repeat: no-repeat;
  background-size: 26px;
  background-color: red;
  cursor: move;
}

.visitedNode {
  background-color: #5cd3e6;
  position: relative;
  border: 0.1px solid white;
  animation: visitedNode 0.5s ease-out;
  border-left: none;
  border-top: none;
}

.path {
  background-color: rgb(255, 255, 75);
  position: relative;
  border: 0.1px solid white;
  animation: pathNode 0.5s ease-out;
  border-left: none;
  border-top: none;
}

@keyframes visitedNode {
  0% {
    transform: scale(0.1);
    border-radius: 50%;
    background-color: rgb(255, 255, 53);
  }
  20% {
    transform: scale(0.2);
    border-radius: 50%;
    background-color: #445d9b;
  }
  40% {
    transform: scale(0.4);
    border-radius: 50%;
    background-color: #445d9b;
  }
  60% {
    transform: scale(0.6);
    border-radius: 50%;
    background-color: #40e2b7;
  }
  80% {
    transform: scale(0.8);
    border-radius: 50%;
    background-color: #40e2b7;
  }
  100% {
    transform: scale(1);
  }
}

@media (max-width: 700px) {
  .node {
    height: 22px;
    width: 22px;
  }
  .finishNode {
    background-size: 20px;
  }
  .startNode {
    background-size: 20px;
  }
}
