.navbar {
  width: 100%;
  background-color: #34495e;
  color: white;
  font-size: 16px;
  user-select: none;
}
.title {
  font-size: 1.4em;
}
.dropdown-list {
  font-size: 16px;
}
.buttons {
  margin-left: 6px;
  display: flex;
  flex-direction: row;
  color: white;
}
.list {
  color: rgb(216, 216, 216);
  background-color: transparent;
  border: none;
  margin-left: 10px;
  margin-right: 10px;
}

.list:hover {
  cursor: pointer;
  color: white;
}

#nav-dropdown {
  color: rgb(216, 216, 216);
}

#nav-dropdown:hover {
  color: white;
}

.disabled {
  cursor: not-allowed;
  color: grey;
  margin-left: 10px;
}

.disabled:hover {
  cursor: not-allowed;
  color: #aaa;
}

@media (max-width: 700px) {
  .navbar {
    font-size: 14px;
  }
  .list {
    margin-left: 5px;
    margin-right: 5px;
  }
  .buttons {
    margin-left: 10px;
  }
  .dropdown-list {
    font-size: 14px;
  }
}

@media (max-width: 500px) {
  .navbar {
    font-size: 10px;
  }
  .list {
    margin-left: 5px;
    margin-right: 5px;
  }
  .buttons {
    margin-left: 10px;
  }
  .dropdown-list {
    font-size: 10px;
  }
}
